const Brand = {
  allBrand: {
    url: "/brand",
    method: "GET",
  },
  addBrand: {
    url: "/brand",
    method: "POST",
  },
  updateBrand: (id) => ({
    url: `/brand/${id}`,
    method: "PUT",
  }),
  deleteBrand: (id) => ({
    url: `/brand/${id}`,
    method: "DELETE",
  }),
};
export default Brand;
