const Banner = {
  allBanner: {
    url: "/banner",
    method: "GET",
  },
  addBanner: {
    url: "/banner",
    method: "POST",
  },
  updateBanner: (id) => ({
    url: `/banner/${id}`,
    method: "PUT",
  }),
  deleteBanner: (id) => ({
    url: `/banner/${id}`,
    method: "DELETE",
  }),
};
export default Banner;
